  left: auto;
  right: 25px; }

[dir="rtl"] .responsive-block-editor-addons-tm__arrow-inside.responsive-block-editor-addons-slick-carousel .slick-next {
  left: 25px;
  right: auto; }

[dir="rtl"] .responsive-block-editor-addons-tm__arrow-outside.responsive-block-editor-addons-slick-carousel .slick-next {
  left: -45px;
